var forge = require('node-forge');
// Libreria para encripcion RSA/ECB/OAEPWithSHA-256AndMGF1Padding
// Tabapay genera 2 llaves una publica y una privada ligadas a una referencia por cliente
// Tabapay proporciona una llave publica para encriptar
// se guarda en variables de ambiente la llave publica Tabapay
// para desencriptar se necesita la llave privada, solo Tabapay la tiene

const base64EncodeUrl = (s) => {
    return s
        .replace(/\+/g, '-')
        .replace(/\//g, '_')
        .replace(/=+$/, '');
};
const base64DecodeUrl = (s) => {
    const str = `${s}===`.slice(0, s.length + (s.length % 4));
    return str.replace(/-/g, '+').replace(/_/g, '/');
};
const formatPemPublicKey = (str) => {
    return `-----BEGIN PUBLIC KEY-----\r\n${str}\r\n-----END PUBLIC KEY-----\r\n`;
};
const encrypData = (keyData, cardNumber, expDate) => {
    const cardData = `${cardNumber}|${expDate}|`;
    // decode the base 64 url encoded key data
    const decodedKey = base64DecodeUrl(keyData);
    // format the decoded key using PEM format
    const pemKey = formatPemPublicKey(decodedKey);
    // use forge to get public key
    const publicKey = forge.pki.publicKeyFromPem(pemKey);
    // encrypt the card data
    const encrypted = publicKey.encrypt(cardData, 'RSA-OAEP', {
        md: forge.md.sha256.create(),
        mgf1: {
            md: forge.md.sha256.create(),
        },
    });
    const encryptedBase64 = forge.util.encode64(encrypted);
    return base64EncodeUrl(encryptedBase64);
};

export default encrypData;